    height: 45px;
    input {
      width: 100%;
      height: 100%;
      border-style: solid;
      border-width: 2px;
      border-color: rgb(255, 255, 255);
      background-color: rgb(255, 255, 255);
      outline: none;
      padding: 0 25px;
