      &::placeholder {
        font-family: ar-reg;
        font-size: 14px;
        color: #999;
      }
      &:hover, &:focus {
        border-color: #ff9900;
      }
    }
    button {
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translateY(-50%);
      font-size: 18px;
      color: rgb(51, 51, 51);
      transition: all .3s ease-in-out;
      [dir="ltr"] & {
        left: unset;
        right: 20px;
      }
      &:hover, &:focus {
        color: #ff9900;
      }
    }
  }
  .icons {
    width: 280px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .icon-container {
      position: relative;
      font-size: 16px;
      color: #333300;
      margin: 0 0 0 15px;
      [dir="ltr"] & {
        margin: 0 14px 0 0;
      }  
      &:last-child {
        margin: 0;
      }
      a {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      i {
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
      span {
        font-family: ar-reg;
        font-size: 16px;
        transition: all .3s ease-in-out;
      }
      &:hover {
        color: #ff9900;
      }
    }
    .menu-icon {
      display: none;
    }
    .search-icon {
      display: none;
    }
    .user-icon {
      position: relative;
      .switch {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 40px;
        left: 50%;
        z-index: 9;
        transform: translateX(-50%);
        min-width: 130px;
        width: max-content;
        background-color: #fff;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
        color: #333;
        border-radius: 5px;
        padding: 8px 5px;
        transition: all .5s ease-in-out;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        a {
          display: block;
          margin: 3px 0;
          &:hover {
            color: #ff9900;
          }
        }
        span {
          padding: 0 2px;
          font-size: 15px;
        }
      }
      &:hover {
        > span {
          color: #ff9900;
          &::after {
            color: #ff9900;
          }
        }
        .switch {
          top: 30px;
          opacity: 1;
          visibility: visible;
        }
      }
    }
    .cart-icon {
      // width: 50px;
      // height: 50px;
      // background-color: #727071;
      // border-style: solid;
      // border-width: 1px;
      // border-color: rgb(236, 236, 236);
      // border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      .counter {
        clip-path: polygon(0 27%, 50% 0, 100% 27%, 100% 76%, 50% 100%, 0 76%);

        position: absolute;
        top: -7px;
        right: -5px;
        width: 18px;
        height: 20px;
        padding: .5px 0 0 0;
        color: #ffffff;
        background-color: #ff9900;
        font-family: ar-reg;
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
        [dir="ltr"] & {
          right: unset;
          left: -5px;
        }
      }
      &:hover {
        color: #ff9900;
        .counter {
          transform: rotateY(360deg);
          background-color: #333300;
        }
      }
    }
  }
}


