/*--- card start ---*/
.card {
  width: 360px;
  // min-height: 220px;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  &:hover {
    .card-img {
      .overlay {
        opacity: 1;
        visibility: visible;
        button {
          transform: translateX(0%) !important;
        }
      }
      img {
        transform: scale(1.05);
      }
    }
  }
  .card-img {
    height: 360px;
    // border-style: solid;
    // border-width: 2px;
    // border-color: rgb(237, 237, 237);
    // background-color: #f7f7f7;
    margin: 0 0 0px 0;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      transition: all .5s ease-in-out;
      mix-blend-mode: multiply;
      width: 100%;
    }
    .overlay {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(66, 39, 0, 0.702);
      transition: all .5s ease-in-out;
      button {
        clip-path: polygon(0 27%, 50% 0, 100% 27%, 100% 76%, 50% 100%, 0 76%);
        width: 60px;
        height: 70px;
        font-size: 24px;
        color: rgb(255, 255, 255);
        background-color: #ff9900;
        margin: 0 8px;
        transition: all .5s ease-in-out;
        &.card-icon {
          transform: translateX(30%);
          [dir="ltr"] & {
            transform: translateX(-30%);
          }
        }
        &.heart-icon {
          transform: translateX(-30%);
          [dir="ltr"] & {
            transform: translateX(30%);
          }
        }
        &:hover {
          background-color: #422700;
        }
      }
    }
  }
  .card-body {
    // padding: 0 10px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
  }
  .title {
    font-family: ar-reg;
    font-size: 16px;
    color: #333300;
    margin: 20px 0 10px 0;
    // padding: 0 0 5px 0;
    // height: 47px;
    overflow: hidden;
    transition: all .3s ease-in-out;
    &:hover {
      color: #cc6600;
    }
  }
  .price {
    font-family: ar-bold;
    font-size: 18px;
    color: #333300;
    // margin: 0 0 15px 0;
    // padding: 10px 0 5px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    .old {
      font-family: ar-md;
      font-size: 16px;
      color: #999;
      text-decoration: line-through;
      margin: 0 8px;
    }
  }
}

/*--- card end ---*/








