      &::after {
        content: "";
        background-color: rgb(255, 163, 0);
        width: 3px;
        height: 15px;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        [dir="ltr"] & {
          right: unset;
          left: 0;
        }
      }
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      display: block;
      .owl-prev, .owl-next {
        outline: none;
        position: absolute;
        top: 45%;
        transform: translateY(-50%);
        font-size: 36px;
        color: rgba(66, 39, 0, 0.502);
        // background-color: rgba(0, 0, 0, 0.4);
        transition: all .5s ease-in-out;
        &:hover {
          color: #000;
        }
      }
      .owl-prev {
        right: -45px;
      }
      .owl-next {
        left: -45px;
      }
    }
    .owl-dots {
      width: max-content;
      position: absolute;
      left: 50%;
      bottom: -45px;
      transform: translateX(-50%);
      .owl-dot {
        width: 9px;
        height: 9px;
        background-color: #0000004d;
        border-radius: 50%;
        outline: none;
        margin: 0 2.5px;
        &.active {
          background-color: #ff9900;
        }
      }
    }
  }
}
